body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --main-color: #2b2828;
  --color-secondary: #ccc;
  --color-text: rgb(114, 113, 113);
  --color-gradient1: #14b01a;
  --color-gradient2: #a71ae8;
}

svg {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 20vw;
  /* set height to pixels if you want angle to change with screen width */
}

#mycontainer {
  background: -webkit-linear-gradient(
    228deg,
    #14b01a,
    #a71ae8);
  background: linear-gradient(
    222deg,
    #14b01a,
    #a71ae8);
  background: -webkit-linear-gradient(
    228deg,
    var(--color-gradient1),
    var(--color-gradient2)
  );
  background: linear-gradient(
    222deg,
    var(--color-gradient1),
    var(--color-gradient2)
  );
  background-size: 400% 400%;
  position: relative;

  -webkit-animation: AnimationName 7s ease infinite;
  animation: AnimationName 7s ease infinite;
}

@-webkit-keyframes AnimationName {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes AnimationName {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.mycontainer {
  width: 100%;
  min-height: 90vh;
  display: flex;
  align-items: center;
  margin: 1px 0px;
}

body {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

h1 {
  color: rgba(255, 255, 255, 0.849);
  padding: 10vmin 15vmin 10vmin 15vmin;
  font-size: calc(20px + 2vmin);
}

.textWhite {
  color: rgb(219, 204, 204);
}

.row {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.btn {
  min-width: 100px;
  height: 30px;
  border: white 1px solid;
  padding: 5px 20px;
  display: flex;
  color: white;
  align-items: center;
  margin: 0 15vmin;
  font-size: 18px;
  position: absolute;
  bottom: 25vmin;
}
img {
  max-width: 100%;
  max-height: 100%;
}

.block {
  display: flex;
  justify-content: center;
  margin: -120px 0 50px 0;
}
h2 {
  color: #2b2828;
  color: var(--main-color);
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.rotate {
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
  background-color: rgb(92, 85, 85);
  color: yellow;
  width: 90%;
  font-size: calc(16px + 2vmin);
  padding: 20px;
  position: absolute;
  bottom: 110px;
  text-align: center;
}

#container3 {
  background-color: #2b2828;
  background-color: var(--main-color);
}

.divForm {
  align-items: center;
  display: flex;
  justify-content: center;
  width: 100vw;
}

h2 {
  font-size: 2em;
  text-align: center;
  padding-bottom: 10px;
  text-transform: uppercase;
  margin-bottom: 0px;
}

.divForm h2 {
  color: white;
}
.line {
  background-color: white;
  height: 3px;
  width: 80px;
  margin: 0 auto;
  margin-bottom: 70px;
}

.black {
  background-color: black;
  margin-bottom: 30px;
}

form {
  border-radius: 10px;
  padding: 20px;
  width: 80vmin;
}

form input,
textarea {
  background-color: black;
  border-radius: 8px;
  border: none;
}

h4 {
  color: #ffffff;
  text-align: center;
}

.form-group {
  display: flex;
  flex-direction: row-reverse;
  padding-bottom: 3px;
  margin-bottom: 5px;
}

input {
  outline: none;
  border: 0;
  padding: 10px;
  flex: 1 0 auto;
  font-size: 16px;
  font-weight: 300;
  color: #ccc;
  color: var(--color-secondary);
}

[type="submit"] {
  align-items: center;
  background: black;
  border: 1px white solid;
  border-radius: 0px;
  color: white;
  display: flex;
  font-size: 16px;
  font-weight: 500;
  justify-content: center;
  margin: 40px 0 0 0;
  outline: none;
  padding: 10px 0 10px 0;
  text-transform: uppercase;
  width: 100%;
}

textarea {
  width: 99%;
  padding: 10px 0px 0px 10px;
  font-size: 16px;
  font-weight: 300;
  font-family: Arial, Helvetica, sans-serif;
  color: #ccc;
  color: var(--color-secondary);
}

a {
  text-decoration: none;
  color: white;
}

a:hover,
.btn:hover {
  background-color: white;
  color: rgba(0, 0, 0, 0.568);
}

.project {
  display: flex;
  justify-content: space-evenly;
  border-bottom: 1px solid #ccc;
  border-bottom: 1px solid var(--color-secondary);
  height: 450px;
  align-items: flex-start;
}

@media screen and (max-width: 800px) {
  .project {
    display: block;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
  }
  .project div {
    width: 85%;
    margin: 0 auto;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
  }
  .project .description {
    margin-top: 10px;
  }
}

.project div {
  margin-top: 70px;
  padding-bottom: 10px;
  max-width: 450px;
}

.project img {
  width: 500px;
}

.project p {
  color: rgb(114, 113, 113);
  color: var(--color-text);
  margin-bottom: 50px;
}

.button {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px;
  float: right;
}
.button a {
  color: #2b2828;
  color: var(--main-color);
  font-size: 16px;
  text-decoration: underline;
  font-style: italic;
}

